    import { mapState } from "vuex";

    export default {
        data() {
            return {
		        month: 1,
		        year: 1990,
		        everyday: [],
		        config: {
		        	total: [],
		        	continue: []
		        },
		        user: {},
		        result: {},
		        other_height: '500rpx',
		        now: 1,
		        noday: ['', '', '', '', '', '', '', ''],
		        week: 1,
		        date: [],
		        getRed: false,
		        customize: null,
		        page_loading: true,
		        template_message: false,
		        success: false
            }
        },
        computed: {
            ...mapState({
                userInfo: state => state.user.info,
                mallImg: state => state.mallConfig.__wxapp_img.mall,
                checkImg: state => state.mallConfig.plugin.check_in
            })
        },
        methods: {
            toRules() {
				uni.navigateTo({
					url: `/pages/rules/index?url=${encodeURIComponent(this.$api.check_in.index)}&keys=${JSON.stringify(['config', 'rule'])}`,
				});
            },
            toIndex() {
                uni.redirectTo({
                    url: '/pages/index/index'
                });
            },
            getSetting() {
                let that = this;
                that.$request({
                    url: that.$api.check_in.customize,
                }).then(response=>{
                    if(response.code == 0) {
                        that.customize = response.data.list;
                		that.getList();
                    }else {
                    	that.$hideLoading();
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    that.$hideLoading();
                });
            },
            getList() {
                let that = this;
                that.$request({
                    url: that.$api.check_in.index,
                }).then(response=>{
                    that.$hideLoading();
                    if(response.code == 0) {
                        that.config = response.data.config;
                        that.user = response.data.check_in_user;
                        that.template_message = response.data.template_message;
                		that.getEveryday();
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    that.$hideLoading();
                });
            },
		    getEveryday() {
		        let that = this;
		        let month = that.month;
		        let year = that.year;
		        let day = 32;
		        let everyday = [];
		        if (month < 8 && month % 2 == 0 && month != 2) {
		            day = 31;
		        } else if (month > 7 && month % 2 == 1) {
		            day = 31;
		        } else if (month == 2) {
		            if (year % 4 == 0 && year % 100 != 0) {
		                day = 30;
		            } else {
		                day = 29;
		            }
		        }
		        for (let i = 1; i < day; i++) {
		            everyday.push({
		                date: i,
		                check: false
		            })
		        }
		        let noday = that.noday;
		        let before = that.week;
		        for (let z = 0; z < before; z++) {
		            everyday.unshift(noday[z])
		        }
		        if (everyday.length % 7 != 0) {
		            let number = 7 - (everyday.length % 7);
		            for (let y = 0; y < number; y++) {
		                everyday.push(noday[y])
		            }
		        }
		        that.everyday = everyday;
		        that.getDay(year,month);
		    },
            getDay(year, month) {
                let that = this;
                that.$request({
                    url: that.$api.check_in.sign_in_day,
		            data: {
		                month: month,
		                year: year
		            }
                }).then(response=>{
                    that.$hideLoading();
                    if(response.code == 0) {
                        let date = response.data.check_in_day;
                        for(let day of that.everyday) {
                        	if(day) {
	                        	day.check = false;
                        		for(let item of date) {
	                        		if(item == day.date) {
	                        			day.check = true;
	                        		}
                        		}
                        	}
                        }
                		that.$forceUpdate();
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    that.$hideLoading();
                });
            },
			lose() {
		        let month = this.month - 1;
		        let year = this.year;
		        let now = this.now;
		        if (month < 1) {
		            month = 12;
		            year--;
		        }
		        let dayValue = year + "-" + month + '-01';
		        var day = new Date(Date.parse(dayValue.replace(/-/g, '/')));
		        this.week = day.getDay();
		        var myDate = new Date();
		        let nowYear = myDate.getFullYear();
		        let nowMonth = myDate.getMonth() + 1;
		        if (year == nowYear) {
		            if (month > nowMonth) {
		                now = 0;
		            } else if (month < nowMonth) {
		                now = 32;
		            } else {
		                now = myDate.getDate();
		            }
		        } else if (year > nowYear) {
		            now = 0;
		        } else {
		            now = 32;
		        }
		        this.year = year;
		        this.month = month;
		        this.now = now;
		        this.getEveryday();
		    },
		    // 月份增大
		    add() {
		        let month = this.month + 1;
		        let year = this.year;
		        let now = this.now;
		        if (month > 12) {
		            month = 1;
		            year++;
		        }
		        let dayValue = year + "-" + month + '-01';
		        var day = new Date(Date.parse(dayValue.replace(/-/g, '/')));
		        this.week = day.getDay();
		        var myDate = new Date();
		        let nowYear = myDate.getFullYear();
		        let nowMonth = myDate.getMonth() + 1;
		        if (year == nowYear) {
		            if (month > nowMonth) {
		                now = 0;
		            } else if (month < nowMonth) {
		                now = 32;
		            } else {
		                now = myDate.getDate();
		            }
		        } else if (year > nowYear) {
		            now = 0;
		        } else {
		            now = 32;
		        }
		        this.year = year;
		        this.month = month;
		        this.now = now;
		        this.getEveryday();
		    },
            subscribe() {
                this.$subscribe(this.template_message).then(res => {
                    this.getAward(1);
                }).catch(res => {
                    this.getAward(1);
                });
            },
		    getAward(status,day) {
		        let that = this;
                uni.showLoading({
                	mask: true,
                    title: status == 1 ? '签到中...' : '领取中...'
                });
		        that.$request({
                    url: that.$api.check_in.sign_in,
		            data: {
		                status: status,
                    	day: day ? day : 1
		            }
                }).then(response=>{
                    if(response.code == 0) {
	                    let queueId = response.data.queueId;
	                    let token = response.data.token;
	                    that.checkInResult(queueId,token);
                    }else {
                    	uni.hideLoading();
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    uni.hideLoading();
                    that.$event.on(that.$const.EVENT_USER_LOGIN).then(()=>{
                        that.$store.dispatch('user/info');
                        that.getAward(status,day);
                    });
                });
		    },
		    checkInResult(queueId, token) {
		        let that = this;
		        that.$request({
                    url: that.$api.check_in.sign_in_result,
		            data: {
		                queueId: queueId,
                    	token: token
		            }
                }).then(response=>{
                    uni.hideLoading();
                    if(response.code == 0) {
						if (response.data.retry == 1) {
	                        that.checkInResult(queueId, token);
	                    } else {
	                    	that.result = response.data
	                        if (that.result.type == 'integral') {
	                        	that.success = true;
				            } else if (that.result.type == 'balance') {
	                        	that.getRed = true;
				            }
	                    }
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    uni.hideLoading();
                });
		    },
		    changeSwitch() {
                if(this.user.is_remind == 0) {
                    this.$subscribe(this.template_message);
                }
		    },
		    toSwitch (e) {
		        let that = this;
		        let is_remind = 0;
		        if (e.detail.value) {
		            is_remind = 1;
		        }
		        that.$request({
                    url: that.$api.check_in.user,
		            data: {
		                is_remind: is_remind
		            }
                }).then(response=>{
                    that.$hideLoading();
                    if(response.code == 0) {
                    	this.user.is_remind = is_remind
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    that.$hideLoading();
                });
		    },
		    close() {
	            this.success = false;
	            this.getRed = false;
		        this.getList();
		    },
        },

        onShareAppMessage() {
            return this.$shareAppMessage({
                title: this.$children[0].navigationBarTitle,
                path: "/plugins/check_in/index/index",
            });
        },


		onShareTimeline() {
		  // 分享朋友圈beta
		  return this.$shareTimeline({
		    title: this.$children[0].navigationBarTitle,
		    query: {} // 此处填写页面的参数
		  });
		},

        onLoad() { this.$commonLoad.onload();
            let that = this;
            wx.getSystemInfo({
		      success: function(res, rect) {
		      	that.other_height = (res.windowHeight*2 - 500) + 'rpx'
		      }
		    });

            let myDate = new Date();
	        that.year = myDate.getFullYear();
	        that.month = myDate.getMonth() + 1;
	        that.now = myDate.getDate();
	        let dayValue = that.year + "-" + that.month + '-01';
	        let day = new Date(Date.parse(dayValue.replace(/-/g, '/')));
	        that.week = day.getDay();

			wx.showShareMenu({
			  menus: ['shareAppMessage', 'shareTimeline']
			})

            that.$showLoading({
                type: 'global',
                text: '加载中...'
            });
            that.getSetting();
        }
    }
